.active{
    background-color: #b4b8be;;
}

nav{
    background-color: #dcdddf;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
}

.title{
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: black;
}

nav ul{
    display: flex;
    margin: 0;
}

nav ul li {
    list-style: none;
}

nav ul li a{
    display: block;
    text-decoration: none;
    color: black;
    padding: 0.5rem;
    border-radius: 0.5rem;
}


nav ul li a:hover{
    background-color: #b4b8be;
    color: whitesmoke;
}


.menu{
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.75rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

span{
    height: 0.4rem;
    width: 100%;
    background-color: grey;
    border-radius: 0.2rem;
}

ul{
    padding: 0;
}

@media (max-width: 480px) {
    .menu{
        display: flex;
    }
    nav{
        flex-direction: column;
        align-items: start;
    }

    nav ul{
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    nav ul.open{
        display: flex;
    }

    nav ul li{
        width: 100%;
        text-align: center;
    }

    nav ul li a{
        margin: 0.2rem 0.5rem;
    }
}
